<h1>保安模拟器</h1><h2>{{ E.name }}</h2><div style="padding:30px"><el-row :gutter="30"><el-col :span="6" :xs="24"><el-card shadow="always"><template #header>你的信息</template><el-form label-position="top"><el-form-item label="名声"><el-progress :percentage="profile.fame"></el-progress></el-form-item><el-form-item label="公司信任"><el-progress :percentage="profile.trust"></el-progress></el-form-item></el-form></el-card></el-col><el-col :span="8" :xs="24" style="min-height: 300px;"><el-button @click="nextStep" type="primary" :disabled="!canGoNext">继续</el-button><div class="question">{{p?.desc}}</div><br><div class="option" v-for="(o,i) in p?.options||[]" :key="i" @click="makeChoice(o)"><i class="el-icon-right"></i>{{o.desc}}</div></el-col><el-col :span="8" :xs="24"><li class="message" v-for="(m,i) in messages" :key="i" :class="{latest: i==0}">{{m}}</li></el-col></el-row></div>